<template>
  <div class="card">
    <div class="card-body">
      <div class="row">
        <div class="col-xl-12 col-lg-12">
          <div>
            <a-tabs defaultActiveKey="1">
              <a-tab-pane tab="Message History" key="1">
                <a-table
                  :columns="invoiceColumns"
                  :dataSource="messageData"
                  class="utils__scrollTable"
                  :scroll="{ x: '100%' }"
                >
                  <a slot="message" slot-scope="text">{{text}}</a>
                  <button class="btn btn-info" slot="action" slot-scope="text" >{{text}}</button>
                </a-table>
              </a-tab-pane>
            </a-tabs>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  invoiceData,
} from './data.json'

const invoiceColumns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Message',
    dataIndex: 'message',
    key: 'message',
    scopedSlots: { customRender: 'message' },
  },
  {
    title: 'Date',
    dataIndex: 'date',
    key: 'date',
    sorter: (a, b) => a.date - b.date,
    scopedSlots: { customRender: 'date' },
  },
  {
    title: 'Actions',
    dataIndex: 'view',
    key: 'view',
    scopedSlots: { customRender: 'view' },
  },
]

export default {
  data: function () {
    return {
      invoiceData,
      invoiceColumns,
      messageData: [],
    }
  },
  mounted() {
    this.getSmsHistory()
  },
  methods: {
    getSmsHistory() {
      this.axios.get('/msg/history')
        .then(response => {
          // console.log(response.data.data.messages)
          const dt = response.data.data.messages
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              message: dt[i].message,
              date: dt[i].created_at,
              id: dt[i].id,
              key: dt[i].id,
              action: {
                resend: {
                  name: 'Resend',
                  to: dt[i].to,
                  id: dt[i].id,
                },
                delete: {
                  name: 'Delete',
                  url: dt[i].id,
                },
              },
            }
            data.push(userData)
          }
          this.messageData = data
        })
        .catch(error => {
          // console.log(error)
          return error
        })
    },
  },
}
</script>
